import { prisma } from '@/lib/prisma';
import { getSession } from '@/lib/auth';
import Link from 'next/link';
import { Plus, File, Edit, ArrowRight, Calendar } from 'lucide-react';
import { redirect } from 'next/navigation';

async function createTemplate() {
  'use server';
  const session = await getSession();
  if (!session) return;

  const template = await prisma.template.create({
    data: {
      name: 'Nouveau Modèle',
      content: JSON.stringify([]),
      userId: session.user.id,
    },
  });
  
  redirect(`/dashboard/builder/${template.id}`);
}

export default async function DashboardPage() {
  const session = await getSession();
  const templates = await prisma.template.findMany({
    where: { userId: session.user.id },
    orderBy: { updatedAt: 'desc' },
  });

  return (
    <div className="space-y-8">
      <div className="flex flex-col md:flex-row justify-between items-start md:items-end gap-4 pb-2">
        <div>
          <h1 className="text-3xl font-bold text-gray-900 tracking-tight">Tableau de bord</h1>
          <p className="text-gray-500 mt-2">Gérez vos factures et créez de nouveaux modèles.</p>
        </div>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
        {/* Card: Créer nouveau */}
        <form action={createTemplate} className="h-full min-h-[280px]">
          <button type="submit" className="w-full h-full bg-white border-2 border-dashed border-blue-200 rounded-2xl hover:border-blue-400 hover:bg-blue-50/30 transition-all duration-300 flex flex-col items-center justify-center group cursor-pointer shadow-sm hover:shadow-md">
            <div className="w-16 h-16 bg-blue-50 rounded-full flex items-center justify-center group-hover:scale-110 transition-transform duration-300 mb-4 border border-blue-100">
              <Plus className="w-8 h-8 text-blue-600" />
            </div>
            <span className="font-bold text-lg text-gray-900 group-hover:text-blue-700 transition-colors">Nouveau Modèle</span>
            <span className="text-sm text-gray-500 mt-1 group-hover:text-blue-600/70">Commencer de zéro</span>
          </button>
        </form>

        {/* Cards: Liste des modèles */}
        {templates.map((template) => (
          <div key={template.id} className="group bg-white rounded-2xl border border-gray-100 shadow-sm hover:shadow-xl hover:-translate-y-1 transition-all duration-300 flex flex-col overflow-hidden relative">
              {/* Preview Area */}
              <div className="h-40 bg-gradient-to-br from-gray-50 to-white border-b border-gray-100 flex items-center justify-center relative overflow-hidden group-hover:from-blue-50/30 group-hover:to-white transition-colors">
                {/* Subtle pattern */}
                <div className="absolute inset-0 opacity-[0.03] bg-[radial-gradient(#3b82f6_1px,transparent_1px)] [background-size:16px_16px]"></div>
                
                <File className="text-gray-300 w-12 h-12 group-hover:text-blue-500 group-hover:scale-110 transition-all duration-500 relative z-10" />
                
                {/* Quick Edit Action (top right) */}
                <div className="absolute top-3 right-3 opacity-0 group-hover:opacity-100 transition-all duration-300 transform translate-y-2 group-hover:translate-y-0">
                   <Link href={`/dashboard/builder/${template.id}`} className="p-2 bg-white/80 backdrop-blur rounded-full shadow-sm border border-gray-200 text-gray-500 hover:text-blue-600 hover:border-blue-200 block">
                     <Edit size={16} />
                   </Link>
                </div>
              </div>
              
              {/* Content */}
              <div className="p-5 flex flex-col flex-1">
                <div className="mb-5">
                  <h3 className="font-bold text-lg text-gray-900 group-hover:text-blue-600 transition-colors line-clamp-1 mb-1" title={template.name}>{template.name}</h3>
                  <div className="flex items-center text-xs text-gray-400 gap-1.5">
                    <Calendar size={12} />
                    <span>Modifié le {new Date(template.updatedAt).toLocaleDateString()}</span>
                  </div>
                </div>
                
                <div className="mt-auto grid grid-cols-2 gap-3">
                  <Link href={`/dashboard/builder/${template.id}`} className="flex items-center justify-center py-2.5 text-sm font-medium text-gray-600 bg-gray-50 rounded-xl hover:bg-gray-100 hover:text-gray-900 transition-colors">
                    Éditer
                  </Link>
                  <Link href={`/dashboard/generate/${template.id}`} className="flex items-center justify-center py-2.5 text-sm font-medium text-white bg-blue-600 rounded-xl hover:bg-blue-700 shadow-lg shadow-blue-200 hover:shadow-blue-300 transition-all gap-1.5 group/btn">
                      PDF <ArrowRight size={14} className="group-hover/btn:translate-x-0.5 transition-transform" />
                  </Link>
                </div>
              </div>
          </div>
        ))}
      </div>
    </div>
  );
}
